import * as axios from 'axios'
import React from 'react'
import style from './FriendsPage.module.css'

class FriendsPageAPI extends React.Component {
  // componentDidMount() {
  //   axios
  //     .get(
  //       `https://social-network.samuraijs.com/api/1.0/users?page=${this.props.currentPage}&count=${this.props.pageSize}`
  //     )
  //     .then((response) => {
  //       this.props.setUsers(response.data.items)
  //       this.props.setTotalUsersCount(response.data.totalCount)
  //     })
  // }

  getFriends = () => {
    if (this.props.users.length === 0) {
      axios
        .get(
          'https://social-network.samuraijs.com/api/1.0/users?page=${this.props.currentPage}&count=${this.props.pageSize}'
        )
        .then((response) => {
          this.props.setUsers(response.data.items)
          this.props.setTotalUsersCount(response.data.totalCount)
        })
    }
  }

  onPageChanged = (pageNumber) => {
    this.props.setCurrentPage(pageNumber)
    axios
      .get(
        `https://social-network.samuraijs.com/api/1.0/users?page=${pageNumber}&count=${this.props.pageSize}`
      )
      .then((response) => {
        this.props.setUsers(response.data.items)
      })
  }

  render() {
    let pageCount = Math.ceil(this.props.totalUsersCount / this.props.pageSize)

    let pages = []

    for (let i = 1; i <= pageCount; i++) {
      pages.push(i)
    }

    return (
      <div>
        <div className={style.title}>Friends</div>
        <button onClick={() => this.getFriends()}>Обновить</button>
        <div>
          {pages.map((p) => {
            return (
              <span
                className={this.props.currentPage === p && style.selectedPage}
                onClick={(e) => {
                  this.onPageChanged(p)
                }}
              >
                {p}
              </span>
            )
          })}
        </div>
        <div>
          {this.props.users.map((u) => (
            <div key={u.id}>
              <span>
                <div>
                  <img
                    className={style.photoUrl}
                    src={
                      u.photos.small != null
                        ? u.photos.small
                        : 'https://www.purina.com.au/-/media/project/purina/main/breeds/dog/dog_pugs_desktop.jpg?h=475&la=en&w=825&hash=DA4A958DC725B9B4A91C6BFE5EE822EA'
                    }
                  />
                </div>
                <div>
                  {u.followed ? (
                    <button
                      onClick={() => {
                        this.props.unfollow(u.id)
                      }}
                    >
                      Отписаться
                    </button>
                  ) : (
                    <button
                      onClick={() => {
                        this.props.follow(u.id)
                      }}
                    >
                      Подписаться
                    </button>
                  )}
                </div>
              </span>
              <span>
                <span>
                  <div>{u.name}</div>
                  <div>{u.status}</div>
                </span>
                <span>
                  <div>{'u.location.country'}</div>
                  <div>{'u.location.city'}</div>
                </span>
              </span>
            </div>
          ))}
        </div>
      </div>
    )
  }
}

export default FriendsPageAPI
