import React from 'react'
import { connect } from 'react-redux'
import { followAC, setUsersAC, unfollowAC, setCurrentPageAC, setTotalUsersCountAC } from '../../../redux/friendsReducer'
import FriendsPageAPI from './FriendsPageAPI'

let mapStateToProps = (state) => {
  return {
    users: state.friendPage.users,
    pageSize: state.friendPage.pageSize,
    totalUsersCount: state.friendPage.totalUsersCount,
    currentPage: state.friendPage.currentPage
  }
}

let mapDispatchToProps = (dispatch) => {
  return {
    follow: (userId) => {
      dispatch(followAC(userId))
    },
    unfollow: (userId) => {
      dispatch(unfollowAC(userId))
    },
    setUsers: (users) => {
      dispatch(setUsersAC(users))
    },
    setCurrentPage: (pageNumber) => {
      dispatch(setCurrentPageAC(pageNumber))
    },
    setTotalUsersCount: (totalCount) => {
      dispatch(setTotalUsersCountAC(totalCount))
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps) (FriendsPageAPI)